.GetContainer {
  max-width: 100%;
  margin: 0 auto;
  padding: 15px;
  display: flex;
  justify-content: center;
  background-color: rgb(9, 91, 172);
}

label {
  display: flex;
  align-items: center;
  width: 600px;
  background: #fff;
  border-radius: 5px;
  list-style-type: none;
  color: rgb(0, 0, 0);
}

input {
  padding-left: 10px;
  width: 100%;
  height: 40px;
  border: 0;
  border-radius: 5px;
  outline: none;
  font-size: 24px;
}

input:focus,
input:hover {
  cursor: pointer;
  border: 2px solid rgb(248, 155, 68);
}